<template>
  <div class="novel">
    <div class="title">
      <span>类型：{{$route.query.type == 0 ? '微信内' : '微信外'}}</span>
      <span>ID: {{$route.query.id}}</span>
    </div>t \`
    <Button type="success" size="large" @click="createLink" class="create">新增返回小说链接</Button>
    <Button type="primary" size="large" @click="modal = true" class="update">修改统计代码</Button>
    <Button type="warning" size="large" to="/manage/links/recommend">返回</Button>
    <div style="clear: both;"></div>
    <Table class="novel-table" :columns="novelHeader" :data="novelData"></Table>
    <div class="modal" v-if="modal">
      <h3>请输入统计代码</h3>
      <br>
      <Input size="large" type="text" v-model="listCode" placeholder="请输入统计代码"></Input>
      <br>
      <Button class="btn" type="warning" @click="modalClose">取消</Button>
      <Button class="btn" type="success" @click="modalSubmit">确定</Button>
    </div>
  </div>
</template>

<script>
import Clipboard from "clipboard";
new Clipboard(".copyBtn");
export default {
  data() {
    return {
      domain_names: [],
      modal: false,
      novelData: [],
      listCode: '',
      novelHeader: [
        {
          key: 'title',
          title: '文章标题',
          align: 'center'
        }, {
          title: '复制链接',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('Button', {
              class: "copyBtn",
              attrs: {
                "data-clipboard-text":
                  that.domain_names[0].domain_name +
                  "/recommend/novel/" + params.row._id
              },
              props: {
                size: 'small',
                type: 'success'
              },
              on: {
                click() {
                  that.$Message.info("复制成功");
                }
              }
            }, '复制')
          }
        }, {
          title: 'Action',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('div', [
              h('Button', {
                style: {
                  marginRight: '10px'
                },
                props: {
                  size: 'small',
                  type: 'primary'
                },
                on: {
                  click() {
                    that.$router.push({
                      name: '修改小说返回链接',
                      query: {id: params.row._id, _id: that.$route.query._id}
                    })
                  }
                }
              }, '修改'),
              h('Button', {
                props: {
                  size: 'small',
                  type: 'error'
                },
                on: {
                  click() {
                    that.$Modal.confirm({
                      title: '确认删除吗？',
                      onOk: () => {
                        that.$axios.get('/recommend/novel/del', {params: {id: params.row._id}})
                        .then(res => {
                          that.$Message.info('删除成功')
                          that.novelData.splice(params.index, 1)
                        })
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ]
    }
  },
  mounted() {
    this.getDomain()
  },
  methods: {
    createLink() {
      this.$router.push({
        name: '创建小说返回链接',
        query: this.$route.query
      })
    }, 
    showNovelList() {
      this.$axios.get('/recommend/novel/show', {params: {id: this.$route.query.id}})
      .then(res => {
        this.novelData = res.data.data.reverse()
      })
    },
    getDomain() {
      this.$axios.get("/tuiguang/novel/show").then(res => {
        this.domain_names = res.data.domain_names;
        this.showNovelList()
      });
    },
    modalClose() {
      this.listCode = ''
      this.modal = false
    },
    modalSubmit() {
      this.$axios.post('/recommend/list/update', {
        id: this.$route.query._id,
        listCode: this.listCode
      })
      .then(res => {
        if(res.data.success) {
          this.$Message.info(res.data.success)
          this.modalClose()
        }
      })
    },
  },
}
</script>

<style scoped>
.novel {

}
.title {
  float: left;
}

.title span {
  font-size: 24px;
  padding: 0 15px;
}
.create, .update {
  float: left;
  margin-left: 30px;
}

.novel-table {
  margin-top: 30px;
}

.modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
}
.modal .btn {
  margin-top: 20px;
  margin-left: 20px;
}
</style>
